@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* GOOGLE FONT */

:root {
  --green: #07aba5;
  --pink: #f73bd0;
  --teal: #23f7f0;
  --yellow: #f7ce0a;
  --gold: #ab900f;
  --white: white;
}

body {
  background-image: url(/challenge-20-react-portfolio/static/media/background-multi-color-wood.e93a81ea.jpg);
  background-size: cover;
  background-position: 100%;
}

/* HEADER STYLES */
header {
  padding-top: 15px;
  padding-left: 30px;
  padding-bottom: 20px;
  background-color: #07aba5;
  background-color: var(--green);
  background: linear-gradient(-90deg, #044d4a, #07aba5);
  background: linear-gradient(-90deg, #044d4a, var(--green));
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 9999;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
}

header h1 {
  font-weight: 600;
  font-size: 32px;
  color: white;
  color: var(--white);
  margin: 0;
  padding-top: 10px;
  /* text-shadow: 0 0 10px rgba(0,0,0,0.5); */
}

header a {
  color: white;
  color: var(--white);
  text-decoration: none;
}

header nav {
  padding-top: 10px;
  padding-right: 20px;
  display: flex;
  align-items: center;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

header nav ul li a {
  padding: 10px 0px;
  margin: 0px 25px;
  font-weight: lighter;
  font-size: 1.55vw;
}

header nav ul li a:hover {
  /* color: var(--green); */
  text-shadow: none;
  border-bottom: 2px solid #07aba5;
  border-bottom: 2px solid var(--green);
}

.nav-active {
  border-bottom: 2px solid #07aba5;
  border-bottom: 2px solid var(--green);
}

.nav-inactive {
  border-bottom: none;
}

/* FOOTER STYLES */
footer {
  text-align: center;
  background: linear-gradient(90deg, #044d4a, #07aba5);
  background: linear-gradient(90deg, #044d4a, var(--green));
  height: 40px;
  align-items: center;
  margin-top: 60px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
  position: fixed;
  padding: 10px 10px 0px 10px;
  bottom: 0;
  width: 100%;
}

footer h2 {
  color: white;
  color: var(--white);
  font-size: 14px;
  font-weight: 300;
  /* padding: 0px 0 20px 20px; */
  text-align: center;
  padding: 5px;
}

footer ul,
li {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-decoration: none;
  padding-bottom: 20px;
  padding-left: 5px;
  padding-right: 5px;
}

footer li,
a {
  text-decoration: none;
  list-style-type: none;
}

footer svg:hover {
  fill: #07aba5;
  fill: var(--green);
  stroke: #23f7f0;
  stroke: var(--teal);
}

footer svg {
  fill: white;
}

.content {
  min-height: calc(100vh - 600px);
}

/* IMAGE STYLES */
img {
  max-width: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

/* BODY STYLES */
body {
  font-family: "Open Sans";
  color: #07aba5;
  color: var(--green);
}

/* GENERAL BODY STYLES */
.flex-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.primary-section-container {
  background-color: white;
  background-color: var(--white);
  padding-bottom: 20px;
}

.secondary-section-container {
  background-color: #f7ce0a;
  background-color: var(--yellow);
  padding-bottom: 20px;
}

.primary-section-title {
  font-size: 30px;
  color: white;
  color: var(--white);
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  margin-top: 40px;
  text-align: center;
  width: 100%;
}

.secondary-section-title {
  font-size: 30px;
  color: #07aba5;
  color: var(--green);
  margin-top: 20px;
  text-align: center;
  width: 100%;
}

.section-info {
  width: 70%;
  /* margin: auto; */
  padding: 30px 20px;
}

/* ABOUT ME STYLES */
.about-container {
  /* max-height: 80%; */
  min-height: 400px;
}

.about-info {
  background-color: rgba(245, 245, 245, 0.9);
  border: 2px solid #07aba5;
  border: 2px solid var(--green);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: flex;
  padding-bottom: 30px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.bio-img {
  flex: 1 1 20%;
  margin-right: 20px;
}

.bio-img img {
  border-radius: 50%;
}

.bio-text {
  color: #07aba5;
  color: var(--green);
  font-size: 16px;
  line-height: 1.3;
  flex: 12 1;
  padding: 15px;
}

/* PORTFOLIO/PROJECT STYLES */
.portfolio-container {
  margin-bottom: 40px;
}

.portfolio-list {
  display: flex;
  flex-direction: column;
  align-content: space-around;
  align-items: flex-end;
  flex-wrap: wrap;
  padding-top: 10px;
}

.single-project {
  display: flex;
  margin-right: 10px;
  margin-bottom: 15px;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-evenly;
  max-width: 700px;
}

.single-project a {
  color: #044d4a;
}

.single-project p {
  font-size: 14px;
  font-weight: 400;
}

.single-project span {
  font-weight: 700;
}

.project-img {
  max-width: 5%;
}

/* CONTACT ME STYLES */
.contact-container {
  display: flex;
  flex: wrap 1;
}

.contact-form {
  display: flex;
  flex-direction: column;
  flex: wrap 1;
  align-content: center;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 20px;
  width: 30%;
  background-color: rgba(245, 245, 245, 0.9);
  border: 2px solid #07aba5;
  border: 2px solid var(--green);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: flex;
  padding-bottom: 30px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.contact-form input,
textarea {
  border: 1px solid #07aba5;
  border: 1px solid var(--green);
  display: block;
  padding: 7px 15px;
  font-size: 16px;
  color: #07aba5;
  color: var(--green);
  width: 100%;
  margin-bottom: 15px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.75);
}

.contact-form label {
  margin: 0 5px;
}

.contact-form button:hover {
  background-color: #f7ce0a;
  background-color: var(--yellow);
}

.contact-form button {
  color: white;
  background-color: #07aba5;
  background-color: var(--green);
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 10px;
  padding-top: 10px;
}

/* RESUME STYLES */
.resume-details iframe {
  width: 75%;
  height: 1000px;
  padding-left: 60px;
}

@media screen and (max-width: 980px) {
  header {
    padding-bottom: 0;
    justify-content: center;
    position: relative;
  }

  header h1 {
    width: 100%;
    text-align: center;
  }

  header nav ul {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 100%;
    justify-content: space-between;
  }

  header nav ul li a {
    font-size: 20px;
  }

  .about-info {
    margin: auto;
  }

  footer h2,
  footer div {
    text-align: center;
    width: 100%;
  }

  section {
    padding: 10px 10px;
  }

  .section-info {
    width: 75%;
    padding-bottom: 10px;
  }

  .primary-section-title,
  .secondary-section-title {
    width: 100%;
  }

  .work-item-primary,
  .work-item-secondary {
    flex: 0 1 100%;
    margin-bottom: 20px;
  }

  .resume-img-container img {
    max-width: 90%;
    margin: auto;
  }

  .contact-item {
    font-size: 150%;
    text-align: center;
    margin-bottom: 20px;
  }

  .contact-item {
    flex: 0 1 100%;
  }
}

@media screen and (max-width: 768px) {
  header nav ul {
    flex-direction: row;
    flex-wrap: nowrap;
    text-align: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 100%;
  }

  .about-info {
    margin-bottom: 40px;
    margin-top: 30px;
  }

  section {
    padding: 5px 10px;
  }

  article {
    padding: 10px 10px;
  }

  .bio-img,
  .bio-text {
    flex: 0 1 80%;
    padding: 20px 10px;
    margin-left: auto;
    margin-right: 0;
    align-items: center;
  }
  .bio-text {
    flex: 0 1 100%;
    text-align: center;
    line-height: 1.2;
  }

  .bio-img img {
    max-width: 70%;
  }

  .contact-form {
    width: 70%;
    padding: 20px;
  }
}

@media screen and (max-width: 575px) {
  section {
    padding: 0 10px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

